@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff2") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: bold;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff2") format("woff");
  font-display: swap;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
  background-color: #ffffff;
  margin: 0;
  min-width: 320px;
}

.section-title {
  color: #000000;
  font-size: 25px;
  font-weight: 700;
  line-height: 30px;
  margin: 0;
  margin-bottom: 28px;
}
@media (min-width: 768px) {
  .section-title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .section-title {
    font-size: 53px;
    line-height: 57px;
  }
}

.properties {
  margin: 0;
  line-height: 20px;
}
@media (min-width: 1150px) {
  .properties {
    font-size: 15px;
  }
}

.properties__item {
  display: flex;
  padding: 8px 10px 11px 0;
  border-bottom: 1px solid #ececec;
}

.properties__name {
  width: 97px;
}

.properties__value {
  margin: 0;
}

.button {
  font: 18px/20px "Open sans", "Arial", sans-serif;
  display: grid;
  font-weight: 700;
  text-align: center;
  color: #444444;
  background: transparent;
  padding: 16px 18px;
  cursor: pointer;
  border: none;
  outline: 2px solid #444444;
  outline-offset: -2px;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.2s;
}
@media (min-width: 768px) {
  .button {
    letter-spacing: -0.7px;
  }
}
.button:hover {
  color: #ffffff;
  background-color: #444444;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button:focus-visible {
  box-shadow: 0 0 0 1px #ffffff, 0 0 0 2px #000000;
}

.button--modal {
  width: 100%;
  background-color: #63d1bb;
  color: #ffffff;
  outline: none;
  letter-spacing: -0.2px;
}
.button--modal:hover {
  background-color: rgba(99, 209, 187, 0.7);
}
.button--modal:active {
  background-color: rgba(99, 209, 187, 0.3);
  color: #ffffff;
}

@media (min-width: 768px) {
  .slider {
    display: grid;
    grid-template-columns: 1fr 175px;
  }
}

.slider__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .slider__list {
    grid-column: 1/-1;
    grid-row: 1/2;
  }
}

.slider__item--hide {
  display: none;
}

.slider__buttons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 52px;
  margin-bottom: 38px;
}
@media (min-width: 768px) {
  .slider__buttons {
    grid-column: 2/3;
    grid-row: 1/2;
    align-self: end;
  }
}
@media (min-width: 1150px) {
  .slider__buttons {
    margin-bottom: 48px;
  }
}

.slider__button {
  border: 1px solid #ececec;
  cursor: pointer;
  background: url("../img/huge-arrow-left.svg") no-repeat 47% 49%;
  background-color: transparent;
  transition: 0.3s;
}
.slider__button:hover {
  opacity: 0.6;
}
.slider__button:active {
  opacity: 0.3;
}

.slider__button--next {
  transform: scaleX(-1);
  background-position: 52% 49%;
  border-right: none;
}

.social {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1px;
  padding-top: 4px;
}
@media (min-width: 768px) {
  .social {
    justify-content: start;
    transform: translate(-13px, -2px);
  }
}
@media (min-width: 1150px) {
  .social {
    transform: none;
    margin-left: 49px;
  }
}

.social__item {
  display: flex;
}

.social__link {
  padding: 15px 14px;
  color: #231f20;
  transition: 0.35s;
}
.social__link:hover {
  color: #46c1ae;
}
.social__link:active {
  opacity: 0.3;
}

.modal-container {
  position: fixed;
  z-index: 100;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: grid;
  background-color: rgba(255, 255, 255, 0.9);
}

.modal-container--hide {
  display: none;
}

.modal {
  margin: auto;
  width: 320px;
  height: 336px;
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
  padding: 29px 30px 46px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .modal {
    width: 566px;
    padding: 38px 108px 46px;
  }
}

.modal__title {
  font-size: 25px;
  font-weight: 700;
  line-height: 31px;
  margin: 0 0 15px;
  text-transform: uppercase;
  text-align: center;
  transform: translateX(3px);
}

.modal__group {
  border: none;
  padding: 0;
  margin: 0;
}

.modal__legend {
  text-align: center;
  margin-bottom: 25px;
}

.modal__group-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 7px;
  margin-bottom: 31px;
}

.modal__label {
  font-size: 17px;
  font-weight: 700;
  line-height: 16px;
  padding: 18px 22px;
  box-shadow: inset 0 0 0 2px rgba(68, 68, 68, 0.1);
  flex-grow: 1;
  text-align: center;
  cursor: pointer;
}

.modal__label:hover {
  box-shadow: inset 0 0 0 2px rgba(68, 68, 68, 0.3);
}

.modal__label:active {
  box-shadow: inset 0 0 0 2px #62d1ba;
}

.modal__input:checked + .modal__label {
  box-shadow: inset 0 0 0 2px #63d1bb;
}

.map {
  width: 100%;
  height: 457px;
}

.container {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    margin: 0;
    background-color: #f1f1f1;
  }
}

.header {
  height: 79px;
}
@media (min-width: 768px) {
  .header {
    height: auto;
  }
}
.header--no-js {
  height: auto;
}

.main-nav {
  display: grid;
  grid-template-rows: 79px;
  grid-template-areas: "top";
  font-size: 17px;
  font-weight: 700;
  min-width: 320px;
  --bg-navigation: #f9f9f9;
}
@media (min-width: 768px) {
  .main-nav {
    grid-template-columns: 209px 1fr 309px;
    grid-template-rows: minmax(89px, auto);
    grid-template-areas: "logo . ." ". . delivery";
  }
}
@media (min-width: 1150px) {
  .main-nav {
    grid-template-columns: 1fr 480px 188px 480px 1fr;
    grid-template-areas: ". site-list logo user-list ." ". .        .     delivery  .";
    background-color: var(--bg-navigation);
  }
}

.main-nav.main-nav--inner {
  --bg-navigation: #f1f1f1;
}

.main-nav__logo-wrapper {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-area: top;
}
@media (min-width: 768px) {
  .main-nav__logo-wrapper {
    grid-area: logo;
  }
}
@media (min-width: 1150px) {
  .main-nav__logo-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-column: -1/1;
    grid-row: 1/2;
    background-color: #ffffff;
    border-bottom: 1px solid #ececec;
  }
}

.main-nav__logo {
  z-index: 2;
  place-self: center;
  display: flex;
  padding: 5px;
  transform: translateY(4px);
  transition: 0.2s;
}
.main-nav__logo:hover {
  opacity: 0.6;
}
.main-nav__logo:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__logo {
    padding: 0;
    transform: translateY(3px);
  }
}
@media (min-width: 1150px) {
  .main-nav__logo {
    transform: translateY(3px);
  }
}

@media (min-width: 768px) {
  .main-nav__logo-img {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .main-nav__logo-img {
    width: 149px;
    height: 72px;
  }
}

.main-nav__toggle {
  box-sizing: content-box;
  grid-area: top;
  justify-self: end;
  align-self: center;
  width: 20px;
  height: 14px;
  background: url("../img/menu-close.svg") no-repeat center;
  border: none;
  padding: 5px 2px;
  margin-right: 30px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
  .main-nav__toggle:hover {
    opacity: 0.6;
  }
}
.main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav__toggle--no-js {
  display: none;
}

.main-nav--hide .main-nav__toggle {
  background: url("../img/menu.svg") no-repeat center;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #ffffff;
  z-index: 1;
}
@media (min-width: 768px) {
  .main-nav__list {
    background-color: transparent;
  }
}

.main-nav__item {
  border-bottom: 1px solid #ececec;
}
.main-nav__item:first-child {
  border-top: 1px solid #ececec;
}
@media (min-width: 768px) {
  .main-nav__item:first-child {
    border-top: none;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    border-bottom: none;
  }
}

.main-nav__delivery {
  grid-area: delivery;
  z-index: 2;
  font-size: 15px;
  font-weight: 400;
  text-decoration: none;
  text-align: right;
  padding-right: 50px;
  padding-top: 20px;
  color: #444444;
  width: fit-content;
  margin-left: auto;
}
.main-nav__delivery:hover {
  opacity: 0.6;
}
.main-nav__delivery:active {
  opacity: 0.3;
}
@media (max-width: 768px) {
  .main-nav__delivery {
    display: none;
  }
}

@media (max-width: 768px) {
  .main-nav--hide .main-nav__list {
    display: none;
  }
}
@media (min-width: 768px) {
  .site-list {
    display: flex;
    flex-wrap: wrap;
    grid-column: 1/-1;
    grid-row: 2/3;
    font-size: 15px;
    background-color: var(--bg-navigation);
    padding: 0 260px 0 50px;
  }
}
@media (min-width: 1150px) {
  .site-list {
    position: relative;
    grid-area: site-list;
    align-content: flex-start;
    padding: 0;
    padding-left: 14px;
    font-size: 17px;
    background-color: transparent;
  }
}

@media (min-width: 1150px) {
  .site-list__item {
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
}
@media (min-width: 768px) {
  .site-list__item--no-border {
    border: none;
  }
}

.site-list__link {
  display: block;
  padding: 29px 0 26px;
  padding-left: 76px;
  text-decoration: none;
  color: #000000;
  transition: opacity 0.35s;
}
.site-list__link:hover {
  opacity: 0.6;
}
.site-list__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .site-list__link {
    padding: 20px 36px 21px 0;
  }
}
@media (min-width: 1150px) {
  .site-list__link {
    padding: 33px 35px 32px 36px;
  }
}

@media (min-width: 768px) {
  .user-list {
    display: flex;
    flex-wrap: wrap;
    grid-column: 1/-1;
    grid-row: 1/2;
    justify-content: end;
    align-content: flex-start;
    padding-left: 209px;
    padding-right: 50px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }
}
@media (min-width: 1150px) {
  .user-list {
    padding-left: 0;
    padding-right: 52px;
    grid-area: user-list;
    border-bottom: 0;
  }
}

@media (min-width: 768px) {
  .user-list__item {
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
}

.user-list__link {
  position: relative;
  display: flex;
  padding: 28px 30px 28px 76px;
  text-decoration: none;
  color: #000000;
}
@media (min-width: 768px) {
  .user-list__link {
    padding: 33px 0 32px 83px;
    min-height: 89px;
  }
}

@media (min-width: 768px) {
  .user-list__text--only-mobile {
    display: none;
  }
}

.user-list__icon {
  position: absolute;
  left: 31px;
  top: 31px;
  color: rgba(0, 0, 0, 0.2);
  transition: 0.35s;
}
@media (min-width: 768px) {
  .user-list__icon {
    top: 35px;
  }
}

.user-list__link-text {
  transition: 0.35s;
}

.user-list__link:hover .user-list__icon {
  color: #63d1bb;
}

.user-list__link:hover .user-list__link-text {
  opacity: 0.6;
}

.user-list__link:active .user-list__icon {
  opacity: 0.3;
}

.user-list__link:active .user-list__link-text {
  opacity: 0.3;
}

.extra-list {
  display: flex;
  flex-wrap: wrap;
  grid-column: 2/-2;
  grid-row: 2/3;
  font-size: 15px;
  padding: 0 310px 0 51px;
}
@media (max-width: 1150px) {
  .extra-list {
    display: none;
  }
}

.extra-list__link {
  display: flex;
  text-decoration: none;
  padding: 19px 62px 19px 0;
  color: #000000;
  transition: opacity 0.35s;
}
.extra-list__link:hover {
  opacity: 0.6;
}
.extra-list__link:active {
  opacity: 0.3;
}

.hero {
  position: relative;
  background: url("../img/index/hero-mobile.jpg") no-repeat center -18px;
  background-color: #f9f9f9;
  background-size: 320px 373px;
}
@media (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/index/hero-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .hero {
    min-height: 621px;
    background: url("../img/index/hero-tablet.jpg") no-repeat center -67px;
    background-color: #f9f9f9;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .hero {
    background: url("../img/index/hero-tablet@2x.jpg") no-repeat center -67px;
    background-size: 768px auto;
  }
}
@media (min-width: 768px) {
  .hero::after {
    content: "";
    position: absolute;
    bottom: -1px;
    width: 100%;
    height: 24px;
    background: url("../img/zigzag-fill-tablet.svg") repeat-x center;
    background-size: 770px 24px;
  }
}
@media (min-width: 1150px) {
  .hero {
    background: url("../img/index/hero-desktop.jpg") no-repeat center -64px;
    background-color: #f9f9f9;
    margin-bottom: 59px;
  }
}
@media (min-width: 1150px) and (min-resolution: 2dppx) {
  .hero {
    background: url("../img/index/hero-desktop@2x.jpg") no-repeat center -67px;
    background-size: 1150px auto;
  }
}
@media (min-width: 1150px) {
  .hero::after {
    bottom: -7px;
    height: 37px;
    background: url("../img/zigzag-fill-desktop.svg") repeat-x;
    background-size: 1250px 37px;
    background-position: 12px 0;
  }
}

.hero__tagline {
  display: block;
  padding: 73px 30px 0;
  min-height: 283px;
  margin: 0 auto;
  color: #000000;
  text-align: center;
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
}
@media (min-width: 768px) {
  .hero__tagline {
    font-size: 43px;
    line-height: 50px;
    padding: 89px 50px 371px;
  }
}
@media (min-width: 1150px) {
  .hero__tagline {
    font-size: 53px;
    line-height: 57px;
    padding: 95px 50px 355px;
  }
}

.categories__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .categories__list {
    padding: 0 50px;
    display: flex;
    line-height: 30px;
    letter-spacing: 0.6px;
  }
}
@media (min-width: 1150px) {
  .categories__list {
    width: 1050px;
    padding: 0;
    margin: 0 auto;
  }
}

.categories__item {
  display: grid;
  min-height: 103px;
  flex-grow: 1;
  transition: 0.15s ease-in-out;
}
@media (min-width: 768px) {
  .categories__item {
    width: 50%;
  }
  .categories__item:hover {
    transform: translateY(-5px);
  }
}

.categories__item--left {
  background-color: #63d1bb;
}

.categories__item--right {
  background-color: #56c3b2;
}

.categories__link {
  position: relative;
  display: block;
  font-size: 19px;
  font-weight: 700;
  text-decoration: none;
  padding: 24px 160px 31px 30px;
  color: #ffffff;
}
.categories__link::after {
  content: "";
  position: absolute;
  opacity: 0.5;
  transition: 0.12s ease-in-out;
}
.categories__link--left::after {
  width: 40px;
  height: 62px;
  background: url("../img/icons/interior.svg");
  top: 24px;
  right: 36px;
}
@media (min-width: 768px) {
  .categories__link--left::after {
    top: 29px;
  }
}
.categories__link--right::after {
  width: 60px;
  height: 60px;
  background: url("../img/icons/toy-svg.svg");
  top: 21px;
  right: 27px;
}
@media (min-width: 768px) {
  .categories__link--right::after {
    top: 30px;
    right: 33px;
  }
}
.categories__link:hover::after {
  opacity: 0.7;
  transform: translateY(-5px);
}
@media (min-width: 768px) {
  .categories__link:hover::after {
    transform: none;
  }
}
.categories__link:active::after {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .categories__link {
    padding: 25px 50px 46px 36px;
  }
}

.promo {
  padding: 32px 30px 42px;
  display: flex;
  flex-direction: column;
  position: relative;
}
@media (min-width: 768px) {
  .promo {
    display: grid;
    grid-template-columns: 1fr 334px;
    grid-template-areas: "top    top" "desc   image" "footer   image";
    column-gap: 42px;
    padding: 40px 50px 68px;
    font-size: 17px;
    line-height: 30px;
    margin-bottom: 26px;
  }
}
@media (min-width: 1150px) {
  .promo {
    grid-template-columns: 175px 292px 58px 525px;
    grid-template-rows: 121px 1fr;
    grid-template-areas: "title  sub-title . image" ".      desc      . image" ".      footer    . image";
    gap: 0;
    padding: 0;
    padding-bottom: 96px;
    width: 1050px;
    margin: 0 auto;
  }
}

.promo__title {
  font-size: 17px;
  font-weight: 700;
  line-height: 20px;
  color: #63d1bb;
  text-transform: uppercase;
  margin: 0 0 17px;
}
.promo__title::after {
  content: "";
  position: absolute;
  height: 6px;
  width: 100%;
  left: 0;
  bottom: 0;
  background: url("../img/zigzag-line-desktop.svg") repeat-x;
  background-size: left center;
}
@media (min-width: 768px) {
  .promo__title::after {
    height: 17px;
    background-size: 772px 17px;
  }
}
@media (min-width: 1150px) {
  .promo__title::after {
    height: 23px;
    background-size: 1050px 23px;
  }
}
@media (min-width: 768px) {
  .promo__title {
    grid-area: top;
    justify-self: end;
    width: 100px;
    text-align: center;
    margin-right: 13px;
    margin-top: 6px;
    line-height: 17px;
  }
}
@media (min-width: 1150px) {
  .promo__title {
    grid-area: title;
    margin: 69px 42px 0 0;
  }
}

.promo__subtitle {
  order: -1;
  margin: 0 0 4px;
}
@media (min-width: 768px) {
  .promo__subtitle {
    grid-area: top;
    margin-bottom: 38px;
  }
}
@media (min-width: 1150px) {
  .promo__subtitle {
    grid-column: 2/-1;
    grid-row: 1/2;
    z-index: 2;
    margin: 0;
    margin-top: 61px;
  }
}

.promo__wrapper-desc {
  grid-area: desc;
}

.promo__desc {
  margin: 0 0 21px;
}
@media (min-width: 768px) {
  .promo__desc {
    margin-bottom: 40px;
  }
}
@media (min-width: 1150px) {
  .promo__desc {
    margin: 33px 0 52px;
  }
